<template>
  <div>
	<div class="row">
	  <div class="fade-in col-lg-6 col-md-12" v-for="person in filteredlist">
        <a href="javascript:void(0)" style="a:hover{color:#3756b5}" v-on:click="modal1 = true">
		<card>
			<p class="card-text">
				<img style="float:left;margin-right:20px" height="150" width="150" :src="valimage(person)">
				<h4 class="card-title">{{person.Name}}</h4>
				Class of {{person.Batch}}<br>
				{{person['Department and Program']}}<br>
				Sectors of Interest: {{person['Sector of Interest']}}
			</p>
		</card>
		</a>
      </div>
    </div>
	<div class="row">
	<div class="col-lg-12 col-md-6">
	<paginate
		v-model="$store.state.upage"
		:page-count="totalpages"
		:page-range="3"
		:margin-pages="0"
		:click-handler="clickCallback"
		:prev-text="'<'"
		:next-text="'>'"
		:container-class="'pagination pagination-sm pagination-pills justify-content-center'"
		:page-class="'page-item'"
		:page-link-class="'page-link'"
		:prev-class="'page-item'"
		:prev-link-class="'page-link'"
		:next-class="'page-item'"
		:next-link-class="'page-link'"
		:hide-prev-next="true">
	</paginate>
	</div>
	</div>
	<modal :show.sync="modal1">
		<template slot="header">
			<h5 class="modal-title" id="exampleModalLabel">Please Login</h5>
		</template>
		<div>
			This is a restricted area and you cannot proceed without login! Please Login to Continue...
		</div>
		<template slot="footer">
			<base-button type="danger" @click="modal1 = false">Close</base-button>
			<base-button type="success" @click="$router.replace('login')">Login</base-button>
		</template>
	</modal>
  </div>
</template>
<style>
.pagination-pills .page-item .page-link {
  border-radius: 50rem !important;
  margin-left: 0.25rem;
  line-height: 1;
}

.pagination-pills .page-item:first-child .page-link {
  margin-left: 0;
}
</style>
<script>
	import firebase from "firebase";
	import { Modal } from "@/components";
	
	export default {
		data() {
			return {
				totalpages: 1,
				page: 1,
				modal1: false,
			}
		},
		components: {
			Modal
		},
		methods: {
			updateuser (id) {
				this.$store.commit('curr_user', id)
			},
			valimage (person) {
				if (person.img){
					return person.img;
				}else{
					return 'img/def_user.jpg'
				}
			},
			notif () {
				this.$notify({type: 'danger', horizontalAlign: 'center', message: 'Viewing as Guest!'})
			},
			clickCallback (pageNum) {
				if(pageNum > 2){
					this.$store.commit('reset_upage');
					this.modal1 = true;
				}
			},
			loadData () {
				if(!this.$store.state.unsecuredloaded) {
					this.$store.commit('load', true);
					var rawdata = [];
					var _this = this;
					const db = firebase.firestore();
					db.collection('unsecured').doc('data').get().then(function(doc) {
						if (doc.exists) {
							const fetched = doc.data();
							rawdata = Object.keys(fetched).map(key => {
								return fetched[key];
							});
							_this.$store.commit('set_unsecuredUsers', rawdata);
						}
					});
					this.$store.commit('load', false);
				}
			},
		},
		computed: {
			filteredlist() {
				var userlist = this.$store.state.unsecuredUsers;
				var filtered = userlist.filter(m => (m.Name.toLowerCase().indexOf(this.$store.state.search) > -1 || m.Batch.toString().indexOf(this.$store.state.search) > -1));
				var num = filtered.length;
				this.totalpages = Math.ceil(num/12);
				var toShow = [];
				var init = this.$store.state.upage - 1;
				for(var i = (init)*12; (i < (init + 1)*12) && (i < num); i++){
					toShow.push(filtered[i]);
				}
				return toShow
			},
		},
		created: function(){
			this.loadData();
			this.notif();
		},
		beforeDestroy: function(){
			this.$store.commit('searchQuery', '');
		}
	}
</script>